@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  box-sizing: border-box;
}

#focus {
  width: calc(600px + 10vw);
  height: 76vh;
  margin-top: -34vh;
}
#focus .top_row ul .unfocues_file {
  color: #85807a;
  background-color: #353330;
}
#focus .top_row ul .focues_file {
  color: #F7F4ED;
  background-color: #00798C;
}

.heading {
  background-color: #353330;
  height: calc(54vh - 80px);
  width: 100%;
  transition: left 0.5s;
  transition: right 0.5s;
  position: relative;
  background-image: url("../imgs/main_background.png");
}
.heading .rust_log {
  position: absolute;
  width: 200px;
  height: 200px;
}
.heading .rust_log1 {
  left: -20px;
  bottom: -29px;
  transform: rotate(20deg);
  transform: scale(200px);
}
.heading .rust_log2 {
  right: -20px;
  top: 20px;
  transform: rotate(-16deg);
}
.heading .heading-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: left 0.5s;
  left: calc(50% - (55vh - 4vw) / 2);
  top: calc(50% - 11.5vh);
  width: calc(55vh - 4vw);
  height: 23vh;
}
.heading .heading-container h1 {
  color: #F7F4ED;
  font-size: calc(25vh - 4vw);
  margin-left: 11.5vh;
}
.heading .heading-container h1 .cursor {
  width: calc(55vh - 4vw);
  height: 23vh;
  z-index: 1;
  animation: blink 1.1s infinite 1s;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
.heading .heading-container h2 {
  color: #F7F4ED;
  font-size: 2vh;
}

body .cards {
  width: 100%;
  height: 100%;
  position: relative;
}
body .cards .t_window_div {
  border: 2px solid #85807a;
  background-color: #262523;
  border-radius: 3px;
  font-family: "Roboto Mono", monospace;
  font-size: 16px;
  transition: width 500ms, height 500ms, margin 500ms;
  overflow-y: none;
}
body .cards .t_window_div .top_row {
  width: 100%;
  height: 25px;
  background-color: #353330;
  float: right;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #4d4a46;
}
body .cards .t_window_div .top_row ul {
  list-style: none;
}
body .cards .t_window_div .top_row ul .unfocues_file {
  color: #F7F4ED;
  background-color: #00798C;
  border-radius: 2px;
}
body .cards .t_window_div .top_row ul .focues_file {
  color: #85807a;
  border-radius: 2px;
  background-color: #353330;
}
body .cards .t_window_div .top_row ul li {
  font-size: 17px;
  float: left;
  margin-left: 10px;
  font-size: 12px;
  color: #85807a;
}
body .cards .t_window_div .title_bar {
  border-radius: 3px;
  height: 30px;
  width: 100%;
  background-color: #4d4a46;
  color: #F7F4ED;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #85807a;
}
body .cards .t_window_div .title_bar p {
  font-family: "Roboto Mono", monospace;
  font-size: 16px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 2px;
}
body .cards .t_window_div .title_bar .icons {
  height: 100%;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
body .cards .t_window_div .title_bar .icons .button {
  height: 90%;
  width: 30px;
  margin: 2px;
  background-size: 15px;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 2px;
  transition: background-color 500ms;
}
body .cards .t_window_div .title_bar .icons .button:hover {
  background-color: #EE4F16;
}
body .cards .t_window_div .title_bar .icons .minimalize {
  background-image: url(../imgs/minim.png);
}
body .cards .t_window_div .title_bar .icons .fullscreen {
  background-image: url(../imgs/fulls.png);
}
body .cards .t_window_div .title_bar .icons .close {
  background-image: url(../imgs/close.png);
}
body .cards .line_numbers {
  margin-top: 1px;
  height: calc(100% - 30px - 25px);
  width: 30px;
  float: left;
  border-right: 1px solid #4d4a46;
  background-color: #353330;
  overflow-y: hidden;
}
body .cards .line_numbers ol {
  list-style-type: decimal;
  list-style-position: inside;
}
body .cards .line_numbers ol li {
  margin-right: 2px;
  text-align: right;
  display: block;
  color: #85807a;
  counter-increment: customlistcounter;
}
body .cards .line_numbers ol li:before {
  content: counter(customlistcounter) " ";
  counter-increment: item;
}
body .cards h1 {
  color: #00798C;
  font-family: "Cutive Mono", monospace;
  font-size: 16px;
}
body .cards h2 {
  font-size: 17px;
  color: #EE4F16;
}
body .cards p {
  font-size: 17px;
  color: #F7F4ED;
}
body .cards a {
  font-size: 17px;
  color: #EE4F16;
}

body {
  background-color: #262523;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
body .navbar {
  font-family: "Roboto Mono", monospace;
  position: relative;
  width: 100%;
  height: 60px;
  background-color: #4d4a46;
  display: flex;
  justify-content: end;
  align-items: center;
}
body .navbar ul {
  list-style: none;
}
body .navbar ul li {
  font-size: 17px;
  float: right;
  margin-right: 30px;
}
body .navbar ul li a {
  color: #F7F4ED;
  text-decoration: none;
}
body main {
  width: 100%;
  height: 100%;
}
body main .index {
  position: absolute;
  background-color: #262523;
  width: 100%;
}
body main .index .cards {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
body main .index .cards .t_window_div {
  width: 600px;
  margin: 10px;
  height: 46vh;
}
body main .index .navbar {
  margin-top: 0;
  font-family: "Roboto Mono", monospace;
  width: 100%;
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #4d4a46;
  display: flex;
  justify-content: end;
  align-items: center;
}
body main .index .navbar ul {
  list-style: none;
}
body main .index .navbar ul li {
  font-size: 17px;
  float: right;
  margin-right: 30px;
}
body main .index .navbar ul li a {
  color: #F7F4ED;
  text-decoration: none;
}
body main .status {
  position: absolute;
  background-color: #EE4F16;
  width: 100%;
}
body main .learn {
  position: absolute;
  width: 100%;
  height: calc(100% - 60px);
}
body main .learn .cards {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 100% calc(40% - 10px);
  grid-template-rows: 70% calc(30% - 10px);
  background-color: #00798C;
  color: #444;
  padding: 10px;
  transition: 300ms;
}
body main .learn .cards .t_window_div .content .sub_content {
  display: none;
}
body main .learn .cards .docs {
  grid-column: 1;
  grid-row: 1/3;
  overflow-y: scroll;
}
body main .learn .cards .docs .title_bar {
  position: absolute;
  width: calc(100% - 24px);
  transition: all 300ms;
}
body main .learn .cards .docs .learn-pages {
  margin-top: 30px;
  float: left;
  width: 272px;
  height: calc(100% - 30px - 17px - 4px);
  background-color: #353330;
  position: absolute;
  transition: width 300ms;
}
body main .learn .cards .docs .learn-pages .menu-button {
  display: none;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 50px;
  height: 40px;
  margin-top: 5px;
}
body main .learn .cards .docs .learn-pages .menu-button .line {
  width: 35px;
  height: 5px;
  border-radius: 3px;
  background-color: #85807a;
}
body main .learn .cards .docs .learn-pages ul {
  list-style: none;
}
body main .learn .cards .docs .learn-pages ul li {
  font-size: 18px;
  height: 40px;
  width: 65%;
  color: #85807a;
  padding: 7px;
  background-color: rgba(0, 0, 0, 0);
  transition: width 500ms, background-color 500ms;
}
body main .learn .cards .docs .learn-pages ul #selected {
  background-color: #4d4a46;
  width: 100%;
  text-align: right;
}
body main .learn .cards .docs .content {
  width: calc(100% - 272px);
  height: calc(100% - 30px);
  margin-top: 30px;
  float: right;
  transition: width 300ms;
}
body main .learn .cards .docs .content code {
  background-color: #353330;
  color: #85807a;
  font-size: 17px;
  padding: 0px 5px;
}
body main .learn .cards .docs .content .rust-c {
  background-color: #4d4a46;
  color: #EE4F16;
}
body main .learn .cards .term {
  grid-column: 2;
  grid-row: 2;
}
body main .learn .cards .editor {
  grid-column: 2;
  grid-row: 1;
}
body main .news {
  position: absolute;
  background-color: tomato;
  width: 100%;
  height: calc(100% - 60px);
}
body main .news #news_div {
  width: 100%;
  height: calc(100% - 17px);
}

@media only screen and (max-width: 1200px) {
  .cards {
    position: relative;
    flex-wrap: wrap;
  }
  #focus {
    width: 600px;
    height: 76vh;
    margin-top: 0;
  }
  .docs .learn-pages {
    width: 50px !important;
  }
  .docs .learn-pages ul {
    display: none;
  }
  .docs .learn-pages .menu-button {
    display: flex !important;
  }
  .docs .content {
    width: calc(100% - 50px) !important;
  }
  #menu_button_pressed {
    width: 272px !important;
  }
  #menu_button_pressed ul {
    display: block;
  }
  #menu_button_pressed .menu-button {
    display: none !important;
  }
}
@media only screen and (max-width: 600px) {
  .editor {
    display: none;
  }
  .term {
    display: none;
  }
  .learn .cards {
    grid-template-columns: 100% 0% !important;
  }
  .learn .cards .docs {
    grid-column: 1/2 !important;
    grid-row: 1/3 !important;
  }
  .learn .cards .docs .title_bar {
    width: calc(100% - 24px) !important;
  }
}